.container {
  display: flex;
  // justify-content: space-between;
  width: 100%;

  @media (max-width: 926px) and (max-height: 428px) and (orientation: landscape) {
    overflow-y: scroll;
  }
}

.carouselColumn {
  // margin: auto;
  padding: 0;
  width: 60%;
  background-color: white;
  // border-radius: 0.6em;
  height: 100%;
}

.productDetails {
  font-family: var(--modal-font);
  font-weight: 400;
  border-radius: 0px 0px 10px 10px;
  width: 40%;
  padding: 10px 20px;
  display: flex;
  flex-direction: column;
  // overflow-y: auto;
  // scrollbar-width: none;
}

.productDetails::-webkit-scrollbar {
  display: none;
}

.excerpt {
  font-size: 1em;
  // margin-top: 1em;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1em;
  border-bottom: 2px solid var(--dark-grey);

  .excerptLeft {
    display: flex;
    align-items: center;
    width: 100%;
  }

  .close {
    border: none;
    background-color: transparent;
    position: absolute;
    top: 6px;
    right: 6px;
    padding: 0;
  }

  .closeBtn {
    width: 35px;
    margin: 0;
  }

  .closeBtn:hover {
    color: black;
  }

  img {
    max-width: 55px;
    margin-right: 6px;
  }

  p {
    font-size: 1em;
    margin: 0;
    padding: 0.7em;
  }

  @media (max-width: 926px) and (max-height: 428px) and (orientation: landscape) {
    margin-top: 0;
    padding: 0.7em 0;
    border-width: 1px;
    font-size: 10px;

    .closeBtn {
      width: 20px;
    }

    img {
      max-width: 35px;
    }

    p {
      font-size: 0.8em;
      padding: 0;
    }
  }
}

.productContent {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  overflow-y: hidden;
}

.contentContainer {
  flex-grow: 1;
  // max-height: 50%;
  overflow-y: auto;
  // overflow-y: auto;
}

.contentContainer::-webkit-scrollbar {
  display: none;
}

.productContainer {
  overflow: auto;
  scrollbar-width: none;
}

.productTitle {
  text-align: left;
  font-weight: 800;
  cursor: pointer;
  font-size: 0.95rem;
  padding: 1em;

  p {
    font-size: 1.5em;
  }

  .productModel {
    margin-bottom: 0;
  }

  @media (max-width: 926px) and (max-height: 428px) and (orientation: landscape) {
    padding: 0.7em;
    font-size: 8px;

    p {
      margin-bottom: 0.5em;
    }
  }
}

.arText {
  text-align: center;
  font-size: 0.5em;
  margin-bottom: 3px;
}

// .productDropdown {
//   border: 1px solid black; // put back if variation dropdown
// }

.productDescription {
  color: black;
  list-style-type: disc;
  font-size: 1em;
  padding-left: 1em;

  li {
    padding: 0;
    text-transform: none;
    font-weight: 400;
    letter-spacing: normal;
  }
}

.productDisclaimer {
  list-style: none;
  font-size: 0.7em;
  font-weight: normal;
  padding-top: 20px;
}

.indicationAndAR {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1em;

  img {
    cursor: pointer;
    max-height: 100%;
    max-width: 95px;
    display: block;
    margin: 0 auto;
  }

  table {
    width: 100%;
    line-height: 1;

    th {
      font-weight: normal;
    }

    td {
      display: inline-block;
    }

    td:nth-child(1) {
      width: 15px;
      text-align: center;
    }

    td:nth-child(2) {
      width: 15px;
      text-align: center;
    }
  }

  @media (max-width: 926px) and (max-height: 428px) and (orientation: landscape) {
    padding: 0.3em 0;
    font-size: 11px;

    img {
      max-height: 40px;
      margin-bottom: 6px;
    }
  }
}

.indication {
  list-style: none;
  font-size: 0.7em;
  letter-spacing: normal;
  color: var(--light-grey);
  padding: 0;
  display: block;
}

.qrCode {
  border: 1px solid var(--dark-grey);
  margin-bottom: 10px;
}

.btnContainer {
  display: flex;
  width: 100%;
  justify-content: space-between;
  margin-top: auto;
}

.btn {
  font-size: 0.6em;
  background-color: transparent;
  border: 1px solid var(--dark-grey);
  padding: 1em;
  margin-bottom: 10px;
  font-weight: 800;
  width: 45%;
  color: var(--light-grey);

  a {
    color: var(--light-grey);
    text-decoration: none;
  }

  a:hover {
    text-decoration: none;
    color: black;
  }

  @media (max-width: 926px) and (max-height: 428px) and (orientation: landscape) {
    font-size: 6px;
    padding: 0.7em 0.5em;
  }
}

.btn:hover {
  color: black;
}

@media (max-width: 926px) and (max-height: 428px) and (orientation: landscape) {
  .carouselColumn {
    width: 45%;
    margin: 0;
    display: flex;
    align-items: center;
  }

  .productDetails {
    // flex: 1;
    width: 55%;
  }
}
